
import { defineComponent, computed, watch, ref } from 'vue'
import checkTable from './cpn/checkTable.vue'
import { selectors } from './config/data'
import { useStore } from 'vuex'
export default defineComponent({
  name: '',
  setup() {
    const store = useStore()
    const supplierName = ref('')
    const purchaseId = ref('')
    const selector = ref('全部')
    let freezeStatus: number | string = ''
    const changeSelector = (item: string) => {
      selector.value = item
      switch (selector.value) {
        case '全部':
          freezeStatus = ''
          break
        case '冻结中':
          freezeStatus = 1
          break
        case '已解冻':
          freezeStatus = 0
          break
      }
      console.log(freezeStatus)
    }
    const searchByCondition = () => {
      store.dispatch('depositModule/getfreezeDeposits', {
        current: 0,
        size: 5
      })
    }
    searchByCondition()
    return {
      selector,
      changeSelector,
      selectors,
      purchaseId,
      searchByCondition,
      supplierName
    }
  },
  components: {
    checkTable
  }
})
